ta.colors,\r\n\t\t\t\t\t\t\tborderColor: '#ffffff',\r\n\t\t\t\t\t\t\tborderWidth: 3,\r\n\t\t\t\t\t\t\thoverBorderWidth: 3,\r\n\t\t\t\t\t\t\thoverOffset: 4,\r\n\t\t\t\t\t\t},\r\n\t\t\t\t\t],\r\n\t\t\t\t},\r\n\t\t\t\toptions: {\r\n\t\t\t\t\tresponsive: true,\r\n\t\t\t\t\tmaintainAspectRatio: true,\r\n\t\t\t\t\tcutout: '58%',\r\n\t\t\t\t\tplugins: {\r\n\t\t\t\t\t\tlegend: {\r\n\t\t\t\t\t\t\tdisplay: false,\r\n\t\t\t\t\t\t},\r\n\t\t\t\t\t\ttooltip: {\r\n\t\t\t\t\t\t\tbackgroundColor: '#1a1a2e',\r\n\t\t\t\t\t\t\ttitleFont: { size: 13, weight: '600' },\r\n\t\t\t\t\t\t\tbodyFont: { size: 12 },\r\n\t\t\t\t\t\t\tpadding: 10,\r\n\t\t\t\t\t\t\tcornerRadius: 8,\r\n\t\t\t\t\t\t\tcallbacks: {\r\n\t\t\t\t\t\t\t\tlabel: function(context) {\r\n\t\t\t\t\t\t\t\t\tconst value = context.parsed\r\n\t\t\t\t\t\t\t\t\tconst percentage = Math.round((value / total) * 100)\r\n\t\t\t\t\t\t\t\t\treturn ` ${context.label}: ${percentage}%`\r\n\t\t\t\t\t\t\t\t},\r\n\t\t\t\t\t\t\t},\r\n\t\t\t\t\t\t},\r\n\t\t\t\t\t},\r\n\t\t\t\t\tlayout: {\r\n\t\t\t\t\t\tpadding: 10,\r\n\t\t\t\t\t},\r\n\t\t\t\t},\r\n\t\t\t\tplugins: [\r\n\t\t\t\t\t{\r\n\t\t\t\t\t\tid: 'percentageLabels',\r\n\t\t\t\t\t\tafterDraw: (chart) => {\r\n\t\t\t\t\t\t\tconst { ctx: context, data: chartDataRef } = chart\r\n\t\t\t\t\t\t\tchart.getDatasetMeta(0).data.forEach((arc, i) => {\r\n\t\t\t\t\t\t\t\tconst value = chartDataRef.datasets[0].data[i]\r\n\t\t\t\t\t\t\t\tconst percentage = Math.round((value / total) * 100)\r\n\t\t\t\t\t\t\t\tconst midAngle = (arc.startAngle + arc.endAngle) / 2\r\n\t\t\t\t\t\t\t\tconst radius = (arc.outerRadius + arc.innerRadius) / 2\r\n\t\t\t\t\t\t\t\tconst x = arc.x + Math.cos(midAngle) * radius\r\n\t\t\t\t\t\t\t\tconst y = arc.y + Math.sin(midAngle) * radius\r\n\r\n\t\t\t\t\t\t\t\tcontext.save()\r\n\t\t\t\t\t\t\t\tcontext.fillStyle = '#ffffff'\r\n\t\t\t\t\t\t\t\tcontext.font = 'bold 12px -apple-system, BlinkMacSystemFont, sans-serif'\r\n\t\t\t\t\t\t\t\tcontext.textAlign = 'center'\r\n\t\t\t\t\t\t\t\tcontext.textBaseline = 'middle'\r\n\t\t\t\t\t\t\t\tcontext.fillText(`${percentage}%`, x, y)\r\n\t\t\t\t\t\t\t\tcontext.restore()\r\n\t\t\t\t\t\t\t})\r\n\t\t\t\t\t\t},\r\n\t\t\t\t\t},\r\n\t\t\t\t],\r\n\t\t\t})\r\n\t\t},\r\n\t},\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.donut-chart {\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\talign-items: center;\r\n\tgap: var(--spacing-lg, 24px);\r\n}\r\n\r\n.donut-chart canvas {\r\n\tmax-width: 220px;\r\n\tmax-height: 220px;\r\n}\r\n\r\n.donut-chart__legend {\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n\tgap: var(--spacing-lg, 24px);\r\n\tflex-wrap: wrap;\r\n}\r\n\r\n.donut-chart__legend-item {\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tgap: 6px;\r\n}\r\n\r\n.donut-chart__legend-dot {\r\n\twidth: 10px;\r\n\theight: 10px;\r\n\tborder-radius: 50%;\r\n\tflex-shrink: 0;\r\n}\r\n\r\n.donut-chart__legend-label {\r\n\tfont-size: 13px;\r\n\tcolor: var(--color-text-secondary, #6b7280);\r\n\tfont-weight: 400;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
