.safety-panel[data-v-03913352] {
	margin-bottom: var(--spacing-xl, 32px);
}

/*  Header  */
.safety-panel__header[data-v-03913352] {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	margin-bottom: var(--spacing-lg, 24px);
}
.safety-panel__header-text[data-v-03913352] {
	flex: 1;
}
.safety-panel__title[data-v-03913352] {
	font-size: 22px;
	font-weight: 600;
	color: var(--color-text-primary, #1a1a2e);
	margin: 0 0 var(--spacing-sm, 8px) 0;
	padding: 0;
	border: none;
}
.safety-panel__subtitle[data-v-03913352] {
	font-size: 14px;
	color: var(--color-text-muted, #9ca3af);
	margin: 0;
	line-height: 1.5;
	max-width: 500px;
}
.safety-panel__collapse-btn[data-v-03913352] {
	background: var(--bg-card, #fff);
	border: 1px solid var(--color-border, #e5e7eb);
	border-radius: 50%;
	width: 36px;
	height: 36px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	color: var(--color-text-secondary, #6b7280);
